<template>
  <div class="_spaceView">
    <div v-if="space">
      <div class="_topSpace">
        <SpacePresentation
          :space="space"
          :context="'full'"
          :can_edit="can_edit_space"
        />
        <div class="_removeBtn">
          <RemoveMenu
            v-if="can_edit_space"
            :remove_text="$t('remove')"
            @remove="removeSpace"
          />
        </div>

        <div class="_contributors">
          <AuthorField
            :label="$t('contributors')"
            :authors_paths="space.$authors"
            :path="space.$path"
            :can_edit="can_edit_space"
            :tag="'h2'"
            :instructions="$t('space_contrib_instr')"
          />
        </div>
      </div>

      <div class="_projectsList">
        <div class="u-sameRow">
          <DLabel :str="$t('list_of_projects')" :tag="'h2'" />
          <button
            type="button"
            class="u-button u-button_red u-button_small"
            v-if="can_edit_space"
            @click="show_create_modal = true"
          >
            <svg
              version="1.1"
              xmlns="http://www.w3.org/2000/svg"
              xmlns:xlink="http://www.w3.org/1999/xlink"
              x="0px"
              y="0px"
              viewBox="0 0 168 168"
              style="enable-background: new 0 0 168 168"
              xml:space="preserve"
            >
              <path
                style="fill: #fc4b60"
                d="M24.6,24.4c-32.8,32.8-32.8,86.1,0,119c32.8,32.8,85.9,32.8,118.7,0c32.8-32.8,32.8-85.9,0-118.7
		C110.5-8.2,57.5-8.2,24.6,24.4z"
              />
              <polygon
                style="fill: #ffbe32"
                points="132.3,73.4 132.3,94.4 94.6,94.4 94.6,132.1 73.6,132.1 73.6,94.4 35.9,94.4 35.9,73.4 
		73.6,73.4 73.6,35.7 94.6,35.7 94.6,73.4 		"
              />
            </svg>
            {{ $t("create_a_project") }}
          </button>
        </div>

        <CreateProject
          v-if="show_create_modal"
          :path="projects_path"
          @close="show_create_modal = false"
          @openNewProject="openNewProject"
        />

        <ProjectsList
          v-if="sorted_projects.length > 0"
          :projects="sorted_projects"
        />
      </div>
    </div>
  </div>
</template>
<script>
import CreateProject from "@/components/modals/CreateProject.vue";
import ProjectsList from "@/components/ProjectsList.vue";
import SpacePresentation from "@/components/space/SpacePresentation.vue";

export default {
  props: {},
  components: {
    CreateProject,
    ProjectsList,
    SpacePresentation,
  },
  data() {
    return {
      space: undefined,
      projects: undefined,
      show_create_modal: false,
    };
  },
  created() {},
  async mounted() {
    await this.getSpace();
    this.$api.join({ room: this.space_path });
    this.$eventHub.$emit("received.space", this.space);

    await this.getProjects();
    this.$api.join({ room: this.projects_path });

    this.$eventHub.$on("folder.removed", this.closeOnRemove);
  },
  beforeDestroy() {
    this.$api.leave({ room: this.space_path });
    this.$api.leave({ room: this.projects_path });

    this.$eventHub.$off("folder.removed", this.closeOnRemove);
  },
  watch: {},
  computed: {
    space_path() {
      return this.createPath({ space_slug: this.$route.params.space_slug });
    },
    projects_path() {
      return this.space_path + "/projects";
    },
    can_edit_space() {
      return this.canLoggedinEditFolder({
        folder_authors: this.space.$authors,
      });
    },
    sorted_projects() {
      if (!this.projects) return [];
      return this.projects
        .slice()
        .filter((p) =>
          this.canLoggedinSeeFolder({
            folder: p,
          })
        )
        .sort(
          (a, b) => +new Date(b.$date_created) - +new Date(a.$date_created)
        );
    },
  },
  methods: {
    async getSpace() {
      this.space = await this.$api
        .getFolder({
          path: this.space_path,
        })
        .catch(() => {
          return;
        });
    },
    async getProjects() {
      this.projects = await this.$api
        .getFolders({
          path: this.projects_path,
        })
        .catch(() => {
          return;
        });
    },
    openNewProject(new_folder_slug) {
      this.show_create_modal = false;
      const url = this.createURLFromPath(
        this.projects_path + "/" + new_folder_slug
      );
      this.$router.push(url);
    },
    closeOnRemove({ path }) {
      if (path === this.space.$path) {
        this.$alertify
          .closeLogOnClick(true)
          .delay(4000)
          .log(this.$t("notifications.space_was_removed"));
        this.$router.push("/");
      }
    },
    async removeSpace() {
      this.fetch_status = "pending";
      this.fetch_error = null;

      try {
        const response = await this.$api.deleteItem({
          path: this.space.$path,
        });
        this.response = response.data;
        this.fetch_status = "success";
      } catch (e) {
        this.fetch_status = "error";
        this.fetch_error = e.response.data;
      }
    },
  },
};
</script>
